import {createAndPaymentOrder, createOrder, getRechargeList} from "../../api/orders";
import {navigateTo} from "../../utils/utils";

export default {
    name: "recharge",
    data(){
        return{
           cardInfo:{},
           rechargeList:[]

        }
    },
    async onLoad(options) {
        this.cardInfo =options;
        console.log(`调试:接收到参数`, options);

        this.rechargeList = await  getRechargeList()
    },
    methods:{
       async handleChoose(e){
            let { rechargeyuan } = e;

           let result = await createAndPaymentOrder({
                price:rechargeyuan,
                cardno:this.cardInfo.cardno
            });
           let { outtradeno  } = result;
           console.log(`调试:支付结果`, result);
           navigateTo({
               url:'../waitResult/waitResult',
               params:{
                   outtradeno,
                   ...this.cardInfo
               }
           });




        }
    }
}
